import React from 'react';
import Evento from './Evento'

const Eventos = ({eventos}) =>  (
        <div>
            <h2>Eventos sobre Bitcoin en Per√∫</h2>
            {eventos.map(evento => {
                <Evento
                 key = {noticias.id}
                 evento = {evento}
                />
            })}
        </div>
    );

export default Eventos;